import React, { useEffect, useState } from "react";
import { useWallet } from "../../../../../../../packages/provider";
import { SwapContract } from "../../../../../../../packages/neo/contracts";
import { IConnectedWallet } from "../../../../../../../packages/neo/wallet/interfaces";
import { ASSET_LIST } from "../../../../consts";
import { toast } from "react-hot-toast";
interface IFarmDetailProps {
  tokenA: string;
  tokenB: string;
  amountA: string;
  amountB: string;
  connectedWallet: IConnectedWallet;
}
const FarmDetail = ({ connectedWallet, tokenA, tokenB }: IFarmDetailProps) => {
  const [myStake, setStake] = useState<any>();
  const [isLoading, setLoading] = useState(true);
  const [error, setError] = useState(false);
  const { network } = useWallet();
  const onClaim = async () => {
    if (connectedWallet) {
      try {
        const res = await new SwapContract(network).claim(
          connectedWallet,
          tokenA,
          tokenB
        );
      } catch (e: any) {
        toast.error(e.description ? e.description : e.message);
      }
    } else {
      toast.error("Please connect wallet");
    }
  };

  useEffect(() => {
    async function fetch() {
      setLoading(true);
      try {
        const res = await new SwapContract(network).getUserStake(
          connectedWallet,
          tokenA,
          tokenB
        );
        setLoading(false);
        setStake(res);
      } catch (e: any) {
        setLoading(false);
        setError(e.message);
        console.log(e);
      }
    }
    fetch();
  }, []);
  if (isLoading) return <div>Loading..</div>;
  return (
    <div>
      <h1 className="title is-5">My liquidity</h1>
      {myStake ? (
        <div>
          <div style={{ alignItems: "center" }} className="media">
            <div className="media-content is-vcentered">
              {ASSET_LIST[tokenA].symbol} / {ASSET_LIST[tokenB].symbol}
              <br />
              <small>
                {myStake.amountA} / {myStake.amountB}
              </small>
            </div>
            <div className="media-right">
              <button
                // onClick={() => setDetail(item)}
                className="button is-light"
              >
                Remove
              </button>
            </div>
          </div>
          <hr />
          <div style={{ alignItems: "center" }} className="media">
            <div className="media-content is-vcentered">
              Claim reward
              <br />
              {ASSET_LIST[tokenA].symbol} / {ASSET_LIST[tokenB].symbol}
              <br />
              <small>
                {myStake.amountA} / {myStake.amountB}
              </small>
            </div>
            <div className="media-right">
              <button onClick={onClaim} className="button is-primary">
                Claim
              </button>
            </div>
          </div>
        </div>
      ) : (
        <div>There is no liquidity with your connected wallet</div>
      )}
    </div>
  );
};

export default FarmDetail;
