import { INetworkType, Network } from "../../../network";
import { IConnectedWallet } from "../../../wallet/interfaces";
import { wallet } from "../../../index";
import { SWAP_SCRIPT_HASH } from "./consts";
import { base64ToHash160, toDecimal } from "../../../utils";
import { u } from "@cityofzion/neon-core";
import { ASSET_LIST } from "../../../../../ui/pages/Swap/consts";
import { parseUserStake } from "./helpers";
import { NEO_SCRIPT_HASH } from "../../../consts";

export class SwapContract {
  network: INetworkType;
  contractHash: string;

  constructor(networkType: INetworkType) {
    this.network = networkType;
    this.contractHash = SWAP_SCRIPT_HASH[networkType];
  }

  provide = async (
    connectedWallet: IConnectedWallet,
    tokenA: string,
    amountA: string,
    tokenB: string,
    amountB: string
  ): Promise<string> => {
    const invokeScript = {
      operation: "addPair",
      scriptHash: this.contractHash,
      args: [
        {
          type: "Address",
          value: connectedWallet.account.address,
        },
        {
          type: "Hash160",
          value: tokenA,
        },
        {
          type: "Integer",
          value: u.BigInteger.fromDecimal(amountA, 8).toString(),
        },
        {
          type: "Hash160",
          value: tokenB,
        },
        {
          type: "Integer",
          value: u.BigInteger.fromDecimal(amountB, 8).toString(),
        },
      ],
    };
    return new wallet.WalletAPI(connectedWallet.key).invoke(
      this.network,
      connectedWallet.account.address,
      invokeScript,
      undefined,
      undefined,
      true
    );
  };

  claim = async (
    connectedWallet: IConnectedWallet,
    tokenA: string,
    tokenB: string
  ): Promise<string> => {
    const invokeScript = {
      operation: "claim",
      scriptHash: this.contractHash,
      args: [
        {
          type: "Address",
          value: connectedWallet.account.address,
        },
        {
          type: "Hash160",
          value: tokenA,
        },
        {
          type: "Hash160",
          value: tokenB,
        },
      ],
    };
    return new wallet.WalletAPI(connectedWallet.key).invoke(
      this.network,
      connectedWallet.account.address,
      invokeScript,
      undefined,
      undefined,
      true
    );
  };

  swap = async (
    connectedWallet: IConnectedWallet,
    tokenA: string,
    amountA: string,
    tokenB: string,
    amountB: string
  ): Promise<string> => {
    const invokeScript = {
      operation: "swap",
      scriptHash: this.contractHash,
      args: [
        {
          type: "Address",
          value: connectedWallet.account.address,
        },
        {
          type: "Hash160",
          value: tokenA,
        },
        {
          type: "Integer",
          value: u.BigInteger.fromDecimal(amountA, 8).toString(),
        },
        {
          type: "Hash160",
          value: tokenB,
        },
        {
          type: "Integer",
          value: u.BigInteger.fromDecimal(amountB, 8).toString(),
        },
      ],
    };
    return new wallet.WalletAPI(connectedWallet.key).invoke(
      this.network,
      connectedWallet.account.address,
      invokeScript,
      undefined,
      undefined,
      true
    );
  };

  getPair = async (
    tokenA: string,
    tokenB: string,
    connectedWallet?: IConnectedWallet
  ) => {
    const scripts: any = [];
    const script = {
      scriptHash: this.contractHash,
      operation: "getPair",
      args: [
        { type: "Hash160", value: tokenA },
        { type: "Hash160", value: tokenB },
      ],
    };
    scripts.push(script);
    if (connectedWallet) {
      const script1 = {
        scriptHash: tokenA,
        operation: "balanceOf",
        args: [{ type: "Address", value: connectedWallet.account.address }],
      };
      const script2 = {
        scriptHash: tokenB,
        operation: "balanceOf",
        args: [{ type: "Address", value: connectedWallet.account.address }],
      };
      scripts.push(script1);
      scripts.push(script2);
    }

    try {
      const res = await Network.read(this.network, scripts, true);
      const obj = {
        // @ts-ignore
        [base64ToHash160(res.stack[0].value[0].value as string)]:
          // @ts-ignore
          res.stack[0].value[2].value,
        // @ts-ignore
        [base64ToHash160(res.stack[0].value[1].value as string)]:
          // @ts-ignore
          res.stack[0].value[3].value,
        balances: {
          [tokenA]: 0,
          [tokenB]: 0,
        },
      };
      if (connectedWallet) {
        // @ts-ignore
        obj.balances[tokenA] = res.stack[1].value;
        // @ts-ignore
        obj.balances[tokenB] = res.stack[2].value;
      }
      return obj;
    } catch (e) {
      // tslint:disable-next-line:no-console
      console.log(e);
      return undefined;
    }
  };

  getPairs = async () => {
    const scripts: any = [];
    const script = {
      scriptHash: this.contractHash,
      operation: "getPairs",
      args: [],
    };
    try {
      const res = await Network.read(this.network, [script], true);
      // @ts-ignore
      return res.stack[0].iterator.map((item) => {
        const tokenA = base64ToHash160(item.value[0].value as string);
        const tokenB = base64ToHash160(item.value[1].value as string);
        const amountA = item.value[2].value;
        const amountB = item.value[3].value;
        return {
          tokenA,
          tokenB,
          amountA: toDecimal(amountA),
          amountB: toDecimal(amountB),
        };
      });
    } catch (e) {
      // tslint:disable-next-line:no-console
      console.log(e);
      return undefined;
    }
  };

  getEstimate = async (tokenA, tokenB, swapToken, amount) => {
    const script = {
      scriptHash: this.contractHash,
      operation: "getEstimate",
      args: [
        { type: "Hash160", value: tokenA },
        { type: "Hash160", value: tokenB },
        { type: "Hash160", value: swapToken },
        {
          type: "Integer",
          value: u.BigInteger.fromDecimal(amount, 8).toString(),
        },
      ],
    };
    try {
      const res = await Network.read(this.network, [script], true);
      // @ts-ignore
      return toDecimal(res.stack[0].value);
    } catch (e) {
      return undefined;
    }
  };

  getUserStake = async (
    connectedWallet: IConnectedWallet,
    tokenA: string,
    tokenB: string
  ) => {
    const script = {
      scriptHash: this.contractHash,
      operation: "getUserStake",
      args: [
        { type: "Address", value: connectedWallet.account.address },
        { type: "Hash160", value: tokenA },
        { type: "Hash160", value: tokenB },
      ],
    };
    try {
      const res = await Network.read(this.network, [script], true);
      return parseUserStake(res.stack);
    } catch (e) {
      // tslint:disable-next-line:no-console
      return undefined;
    }
  };
}
