import { u } from "@cityofzion/neon-core";
import { base64ToHash160, toDecimal } from "../../../utils";

export const getEstimate = (token, amount, pairInfo) => {
  const fixed8TokenAmount = u.BigInteger.fromDecimal(amount, 8).toString();
  let keys = Object.keys(pairInfo);
  keys = keys.filter((k) => k !== token);
  const reservedA = pairInfo[token];
  const reservedB = pairInfo[keys[0]];
  let estimated =
    (parseFloat(fixed8TokenAmount) * parseFloat(reservedB)) /
    parseFloat(reservedA);
  estimated = Math.floor(estimated);
  estimated = toDecimal(estimated.toString());
  return estimated;
};

export const parseUserStake = (stackItem) => {
  return {
    tokenA: base64ToHash160(stackItem[0].value[0].value as string),
    tokenB: base64ToHash160(stackItem[0].value[1].value as string),
    amountA: toDecimal(stackItem[0].value[2].value),
    amountB: toDecimal(stackItem[0].value[3].value),
  };
};
